<script>
import CodeDisplay from '../components/CodeDisplay.vue'

export default {
  components: {
    // eslint-disable-next-line vue/no-unused-components
    CodeDisplay
  },
  created () {
    this.raw = require('../../public/docs/1/index.md')
    let res = this.$mkd.parse(this.raw, {})
    // console.log(res)
    // const re = /(?<![\\])\*{1,1}[^*]+\*{1,1}/
    this.tokens = res
  },
  data: () => ({
    path: '../assets/hello',
    vuetify: true,
    name: '',
    title: '',
    time: '2020-01-23',
    raw: '',
    content:
      '<h1>title</h1>\n' +
      '<CodeDisplay v-model="time"></CodeDisplay>',
    cmOptions: {
      tabSize: 2,
      lineNumbers: true,
      line: true,
      readOnly: 'nocursor'
    },
    theme: 'material',
    tokens: []
  }),
  render: function (createEl) {
    // return createEl(CodeDisplay, { props: { value: this.content, options: this.cmOptions, theme: 'material' } })
    return this.createTag(createEl, 'div', '', 0, this.tokens, {})[1]
  },
  methods: {
    createTag (createEl, tag, type, start, tokens, attr) {
      // returns [new i, createEl(subnode {attr}, [children])]
      let children = []
      let i = start
      for (; i < tokens.length; ++i) {
        if (tokens[i].nesting === 1) {
          // tag opening
          let temp = this.createTag(
            createEl,
            tokens[i].tag,
            tokens[i].type,
            i + 1,
            tokens,
            tokens[i].attr
          )
          i = temp[0]
          children.push(temp[1])
        } else if (tokens[i].nesting === 0) {
          if (tokens[i].type === 'inline') {
            // inline
            children = this.createInline(createEl, tokens[i])
          } else if (tokens[i].type === 'fence') {
            children.push(createEl(
              CodeDisplay,
              {
                props: {
                  value: tokens[i].content,
                  options: this.cmOptions,
                  theme: this.theme,
                  lang: tokens[i].info
                }
              }
            ))
          } else {
            throw SyntaxError('Unsupported tag')
          }
        } else {
          break
        }
      }
      return [i, createEl(tag, {}, children)]
    },
    createInline (createEl, node) {
      // returns ['text', createEl(node1), createEl(node2), ...]
      const res = []
      const children = node.children
      for (let i = 0; i < children.length; ++i) {
        if (children[i].nesting === 1) {
          // tag start
          let temp = this.createInlineTag(
            createEl,
            children[i].tag,
            children[i].type,
            i + 1,
            children,
            children[i].attrs
          )
          i = temp[0]
          res.push(temp[1])
        } else if (children[i].nesting === 0) {
          if (children[i].type === 'text') {
            res.push(children[i].content)
          } else if (children[i].type === 'image') {
            let attributes = {}
            children[i].attrs.forEach(pair => {
              attributes[pair[0]] = pair[1]
            })
            if (attributes['src'] && attributes['src'][0] === '.') {
              attributes['src'] = this.path + attributes['src'].slice(1)
            }
            // let url = './hello/logo.png'
            // let pic = require(url)
            // // console.log(pic)
            res.push(createEl('img', { attrs: attributes }))
            // res.push(createEl('img', { attrs: { src: './docs/logo.png' } }))
          } else {
            res.push(createEl(children[i].tag, {}, children[i].content))
          }
        } else {
          // all the 'close's are supposed to be consumed, throw err here
          throw SyntaxError('Unmatched close tag')
        }
      }
      return res
    },
    createInlineTag (createEl, tag, type, start, tokens, attr) {
      // returns [new i, createEl(subnode {attr}, [children])]
      const children = []
      // handle img and link attributes here
      let attributes = {}
      if (attr) {
        attr.forEach(pair => {
          attributes[pair[0]] = pair[1]
        })
        if (attributes['src'] && attributes['src'][0] === '.') {
          attributes['src'] = this.path + attributes['src'].slice(1)
        }
      }
      // recursive handle others here
      let i = start
      for (; i < tokens.length; ++i) {
        if (tokens[i].nesting === 1) {
          // sub-tag start
          let temp = this.createInlineTag(
            createEl,
            tokens[i].tag,
            tokens[i].type,
            i + 1,
            tokens,
            tokens[i].attrs
          )
          i = temp[0]
          children.push(temp[1])
        } else if (tokens[i].nesting === 0) {
          if (tokens[i].type === 'text') {
            children.push(tokens[i].content)
          } else if (tokens[i].type === 'image') {
            // console.log('img')
          } else {
            children.push(createEl(tokens[i].tag, {}, tokens[i].content))
          }
        } else {
          // 'close' tags, break
          // if the tags dont match, ignore the mismatch and return
          // leaving the error to the caller
          break
        }
      }
      // console.log(attributes)
      return [i, createEl(tag, { attrs: attributes }, children)]
    }
  }
}
</script>
